@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-next {
  width: 1em;
  height: 1em;
  fill: #231F20;
}

.icon-prev {
  width: 1em;
  height: 1em;
  fill: #231F20;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.loader {
  color: #fff;
  position: fixed;
  box-sizing: border-box;
  left: -9999px;
  top: -9999px;
  width: 0;
  height: 0;
  overflow: hidden;
  z-index: 999999;
}

.loader:after, .loader:before {
  box-sizing: border-box;
  display: none;
}

.loader.is-active {
  background-color: rgba(0, 0, 0, 0.85);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.loader.is-active:after, .loader.is-active:before {
  display: block;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes blink {
  from {
    opacity: .5;
  }
  to {
    opacity: 1;
  }
}

.loader[data-text]:before {
  position: fixed;
  left: 0;
  top: 50%;
  color: currentColor;
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
  width: 100%;
  font-size: 14px;
}

.loader[data-text='']:before {
  content: 'Loading';
}

.loader[data-text]:not([data-text='']):before {
  content: attr(data-text);
}

.loader[data-text][data-blink]:before {
  animation: blink 1s linear infinite alternate;
}

.loader-default[data-text]:before {
  top: calc(50% - 63px);
}

.loader-default:after {
  content: '';
  position: fixed;
  width: 48px;
  height: 48px;
  border: solid 8px #fff;
  border-left-color: transparent;
  border-radius: 50%;
  top: calc(50% - 24px);
  left: calc(50% - 24px);
  animation: rotation 1s linear infinite;
}

.loader-default[data-half]:after {
  border-right-color: transparent;
}

.loader-default[data-inverse]:after {
  animation-direction: reverse;
}

.loader-double:after, .loader-double:before {
  content: '';
  position: fixed;
  border-radius: 50%;
  border: solid 8px;
  animation: rotation 1s linear infinite;
}

.loader-double:after {
  width: 48px;
  height: 48px;
  border-color: #fff;
  border-left-color: transparent;
  top: calc(50% - 24px);
  left: calc(50% - 24px);
}

.loader-double:before {
  width: 64px;
  height: 64px;
  border-color: #eb974e;
  border-right-color: transparent;
  animation-duration: 2s;
  top: calc(50% - 32px);
  left: calc(50% - 32px);
}

.loader-bar[data-text]:before {
  top: calc(50% - 40px);
  color: #fff;
}

.loader-bar:after {
  content: '';
  position: fixed;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 20px;
  transform: translate(-50%, -50%);
  background: linear-gradient(-45deg, #4183d7 25%, #52b3d9 25%, #52b3d9 50%, #4183d7 50%, #4183d7 75%, #52b3d9 75%, #52b3d9);
  background-size: 20px 20px;
  box-shadow: inset 0 10px 0 rgba(255, 255, 255, 0.2), 0 0 0 5px rgba(0, 0, 0, 0.2);
  animation: moveBar 1.5s linear infinite reverse;
}

.loader-bar[data-rounded]:after {
  border-radius: 15px;
}

.loader-bar[data-inverse]:after {
  animation-direction: normal;
}

@keyframes moveBar {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 20px 20px;
  }
}

.loader-bar-ping-pong:before {
  content: '';
  width: 200px;
  height: 20px;
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 100px);
  background-color: #000;
}

.loader-bar-ping-pong:after {
  content: '';
  width: 50px;
  height: 20px;
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 100px);
  background-color: #f19;
  animation: moveBarPingPong .5s linear infinite alternate;
}

.loader-bar-ping-pong[data-rounded]:before {
  border-radius: 10px;
}

.loader-bar-ping-pong[data-rounded]:after {
  border-radius: 50%;
  width: 20px;
  animation-name: moveBarPingPongRounded;
}

@keyframes moveBarPingPong {
  0% {
    left: calc(50% - 100px);
  }
  100% {
    left: calc(50% - -50px);
  }
}

@keyframes moveBarPingPongRounded {
  0% {
    left: calc(50% - 100px);
  }
  100% {
    left: calc(50% - -80px);
  }
}

@keyframes corners {
  6% {
    width: 60px;
    height: 15px;
  }
  25% {
    width: 15px;
    height: 15px;
    left: calc(100% - 15px);
    top: 0;
  }
  31% {
    height: 60px;
  }
  50% {
    height: 15px;
    top: calc(100% - 15px);
    left: calc(100% - 15px);
  }
  56% {
    width: 60px;
  }
  75% {
    width: 15px;
    left: 0;
    top: calc(100% - 15px);
  }
  81% {
    height: 60px;
  }
}

.loader-border[data-text]:before {
  color: #fff;
}

.loader-border:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background-color: #ff0;
  animation: corners 3s ease both infinite;
}

.loader-ball:before {
  content: '';
  position: absolute;
  width: 50px;
  height: 50px;
  top: 50%;
  left: 50%;
  margin: -25px 0 0 -25px;
  background-color: #fff;
  border-radius: 50%;
  z-index: 1;
  animation: kick 1s infinite alternate ease-in both;
}

.loader-ball[data-shadow]:before {
  box-shadow: -5px -5px 10px 0 rgba(0, 0, 0, 0.5) inset;
}

.loader-ball:after {
  content: '';
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  width: 45px;
  height: 20px;
  top: calc(50% + 10px);
  left: 50%;
  margin: 0 0 0 -22.5px;
  z-index: 0;
  animation: shadow 1s infinite alternate ease-out both;
}

@keyframes shadow {
  0% {
    background-color: transparent;
    transform: scale(0);
  }
  40% {
    background-color: transparent;
    transform: scale(0);
  }
  95% {
    background-color: rgba(0, 0, 0, 0.75);
    transform: scale(1);
  }
  100% {
    background-color: rgba(0, 0, 0, 0.75);
    transform: scale(1);
  }
}

@keyframes kick {
  0% {
    transform: translateY(-80px) scaleX(0.95);
  }
  90% {
    border-radius: 50%;
  }
  100% {
    transform: translateY(0) scaleX(1);
    border-radius: 50% 50% 20% 20%;
  }
}

.loader-smartphone:after {
  content: '';
  color: #fff;
  font-size: 12px;
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
  line-height: 120px;
  position: fixed;
  left: 50%;
  top: 50%;
  width: 70px;
  height: 130px;
  margin: -65px 0 0 -35px;
  border: solid 5px #fd0;
  border-radius: 10px;
  box-shadow: 0 5px 0 0 #fd0 inset;
  background: radial-gradient(circle at 50% 90%, rgba(0, 0, 0, 0.5) 6px, transparent 6px), linear-gradient(to top, #fd0 22px, transparent 22px), linear-gradient(to top, rgba(0, 0, 0, 0.5) 22px, rgba(0, 0, 0, 0.5) 100%);
  animation: shake 2s cubic-bezier(0.36, 0.07, 0.19, 0.97) both infinite;
}

.loader-smartphone[data-screen='']:after {
  content: 'Loading';
}

.loader-smartphone:not([data-screen='']):after {
  content: attr(data-screen);
}

@keyframes shake {
  5% {
    transform: translate3d(-1px, 0, 0);
  }
  10% {
    transform: translate3d(1px, 0, 0);
  }
  15% {
    transform: translate3d(-1px, 0, 0);
  }
  20% {
    transform: translate3d(1px, 0, 0);
  }
  25% {
    transform: translate3d(-1px, 0, 0);
  }
  30% {
    transform: translate3d(1px, 0, 0);
  }
  35% {
    transform: translate3d(-1px, 0, 0);
  }
  40% {
    transform: translate3d(1px, 0, 0);
  }
  45% {
    transform: translate3d(-1px, 0, 0);
  }
  50% {
    transform: translate3d(1px, 0, 0);
  }
  55% {
    transform: translate3d(-1px, 0, 0);
  }
}

.loader-clock:before {
  content: '';
  position: fixed;
  width: 120px;
  height: 120px;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  overflow: hidden;
  margin: -60px 0 0 -60px;
  background: linear-gradient(to bottom, transparent 50%, #f5f5f5 50%), linear-gradient(90deg, transparent 55px, #2ecc71 55px, #2ecc71 65px, transparent 65px), linear-gradient(to bottom, #f5f5f5 50%, #f5f5f5 50%);
  box-shadow: 0 0 0 10px #f5f5f5 inset, 0 0 0 5px #555, 0 0 0 10px #7b7b7b;
  animation: rotation infinite 2s linear;
}

.loader-clock:after {
  content: '';
  position: fixed;
  width: 60px;
  height: 40px;
  left: 50%;
  top: 50%;
  margin: -20px 0 0 -15px;
  border-radius: 20px 0 0 20px;
  overflow: hidden;
  background: radial-gradient(circle at 14px 20px, #25a25a 10px, transparent 10px), radial-gradient(circle at 14px 20px, #1b7943 14px, transparent 14px), linear-gradient(180deg, transparent 15px, #2ecc71 15px, #2ecc71 25px, transparent 25px);
  animation: rotation infinite 24s linear;
  transform-origin: 15px center;
}

.loader-curtain:before, .loader-curtain:after {
  position: fixed;
  width: 100%;
  top: 50%;
  margin-top: -35px;
  font-size: 70px;
  text-align: center;
  font-family: Helvetica, Arial, sans-serif;
  overflow: hidden;
  line-height: 1.2;
  content: 'Loading';
}

.loader-curtain:before {
  color: #666;
}

.loader-curtain:after {
  color: #fff;
  height: 0;
  animation: curtain 1s linear infinite alternate both;
}

.loader-curtain[data-curtain-text]:not([data-curtain-text='']):before, .loader-curtain[data-curtain-text]:not([data-curtain-text='']):after {
  content: attr(data-curtain-text);
}

.loader-curtain[data-brazilian]:before {
  color: #f1c40f;
}

.loader-curtain[data-brazilian]:after {
  color: #2ecc71;
}

.loader-curtain[data-colorful]:before {
  animation: maskColorful 2s linear infinite alternate both;
}

.loader-curtain[data-colorful]:after {
  animation: curtain 1s linear infinite alternate both, maskColorful-front 2s 1s linear infinite alternate both;
  color: #000;
}

@keyframes maskColorful {
  0% {
    color: #3498db;
  }
  49.5% {
    color: #3498db;
  }
  50.5% {
    color: #e74c3c;
  }
  100% {
    color: #e74c3c;
  }
}

@keyframes maskColorful-front {
  0% {
    color: #2ecc71;
  }
  49.5% {
    color: #2ecc71;
  }
  50.5% {
    color: #f1c40f;
  }
  100% {
    color: #f1c40f;
  }
}

@keyframes curtain {
  0% {
    height: 0;
  }
  100% {
    height: 84px;
  }
}

.loader-music:before, .loader-music:after {
  content: '';
  position: fixed;
  width: 240px;
  height: 240px;
  top: 50%;
  left: 50%;
  margin: -120px 0 0 -120px;
  border-radius: 50%;
  text-align: center;
  line-height: 240px;
  color: #fff;
  font-size: 40px;
  font-family: Helvetica, Arial, sans-serif;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.5);
  letter-spacing: -1px;
}

.loader-music:after {
  backface-visibility: hidden;
}

.loader-music[data-hey-oh]:before, .loader-music[data-hey-oh]:after {
  box-shadow: 0 0 0 10px;
}

.loader-music[data-hey-oh]:before {
  background-color: #fff;
  color: #000;
  animation: coinBack 2.5s linear infinite, oh 5s 1.25s linear infinite both;
}

.loader-music[data-hey-oh]:after {
  background-color: #000;
  animation: coin 2.5s linear infinite, hey 5s linear infinite both;
}

.loader-music[data-no-cry]:before, .loader-music[data-no-cry]:after {
  background: linear-gradient(45deg, #009b3a 50%, #fed100 51%);
  box-shadow: 0 0 0 10px #000;
}

.loader-music[data-no-cry]:before {
  animation: coinBack 2.5s linear infinite, cry 5s 1.25s linear infinite both;
}

.loader-music[data-no-cry]:after {
  animation: coin 2.5s linear infinite, no 5s linear infinite both;
}

.loader-music[data-we-are]:before {
  animation: coinBack 2.5s linear infinite, theWorld 5s 1.25s linear infinite both;
  background: radial-gradient(ellipse at center, #4ecdc4 0%, #556270);
}

.loader-music[data-we-are]:after {
  animation: coin 2.5s linear infinite, weAre 5s linear infinite both;
  background: radial-gradient(ellipse at center, #26d0ce 0%, #1a2980);
}

.loader-music[data-rock-you]:before {
  animation: coinBack 2.5s linear infinite, rockYou 5s 1.25s linear infinite both;
  background: #444;
}

.loader-music[data-rock-you]:after {
  animation: coin 2.5s linear infinite, weWill 5s linear infinite both;
  background: #96281b;
}

@keyframes coin {
  to {
    transform: rotateY(359deg);
  }
}

@keyframes coinBack {
  0% {
    transform: rotateY(180deg);
  }
  50% {
    transform: rotateY(360deg);
  }
  100% {
    transform: rotateY(180deg);
  }
}

@keyframes hey {
  0% {
    content: 'Hey!';
  }
  50% {
    content: 'Let\'s!';
  }
  100% {
    content: 'Hey!';
  }
}

@keyframes oh {
  0% {
    content: 'Oh!';
  }
  50% {
    content: 'Go!';
  }
  100% {
    content: 'Oh!';
  }
}

@keyframes no {
  0% {
    content: 'No...';
  }
  50% {
    content: 'no';
  }
  100% {
    content: 'No...';
  }
}

@keyframes cry {
  0% {
    content: 'woman';
  }
  50% {
    content: 'cry!';
  }
  100% {
    content: 'woman';
  }
}

@keyframes weAre {
  0% {
    content: 'We are';
  }
  50% {
    content: 'we are';
  }
  100% {
    content: 'We are';
  }
}

@keyframes theWorld {
  0% {
    content: 'the world,';
  }
  50% {
    content: 'the children!';
  }
  100% {
    content: 'the world,';
  }
}

@keyframes weWill {
  0% {
    content: 'We will,';
  }
  50% {
    content: 'rock you!';
  }
  100% {
    content: 'We will,';
  }
}

@keyframes rockYou {
  0% {
    content: 'we will';
  }
  50% {
    content: '🤘';
  }
  100% {
    content: 'we will';
  }
}

.loader-pokeball:before {
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  top: 50%;
  left: 50%;
  margin: -50px 0 0 -50px;
  background: linear-gradient(to bottom, #f00 42%, #000 42%, #000 58%, #fff 58%);
  background-repeat: no-repeat;
  background-color: #fff;
  border-radius: 50%;
  z-index: 1;
  animation: movePokeball 1s linear infinite both;
}

.loader-pokeball:after {
  content: '';
  position: absolute;
  width: 24px;
  height: 24px;
  top: 50%;
  left: 50%;
  margin: -12px 0 0 -12px;
  background-color: #fff;
  border-radius: 50%;
  z-index: 2;
  animation: movePokeball 1s linear infinite both, flashPokeball .5s infinite alternate;
  border: solid 2px #000;
  box-shadow: 0 0 0 5px #fff, 0 0 0 10px #000;
}

@keyframes movePokeball {
  0% {
    transform: translateX(0) rotate(0);
  }
  15% {
    transform: translatex(-10px) rotate(-5deg);
  }
  30% {
    transform: translateX(10px) rotate(5deg);
  }
  45% {
    transform: translatex(0) rotate(0);
  }
}

@keyframes flashPokeball {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: #fd0;
  }
}

body {
  position: relative;
}

.overlay {
  display: none;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.preview {
  height: 90vh;
  width: fit-content;
  margin: 5vh auto;
  padding: 10px;
  background-color: #FFFFFF;
  position: relative;
}

.preview img {
  height: 100%;
  max-width: 100%;
}

.btn-next, .btn-prev {
  position: absolute;
  display: block;
  top: calc(50% - 20px);
  height: 40px;
  width: 40px;
  background: transparent;
  color: white;
  transition: 0.3s;
}

.btn-next:hover, .btn-prev:hover {
  transform: scale(1.1);
}

.btn-next svg, .btn-prev svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.btn-prev {
  left: -50px;
}

.btn-next {
  right: -50px;
}

.container {
  width: 600px;
  height: 800px;
  margin: 0 auto;
  padding: 10px;
  overflow: hidden;
}

.big-image {
  width: 580px;
  height: 600px;
  margin: 0 auto;
}

.big-image__img {
  width: 100%;
}

.big-image__img:hover {
  cursor: pointer;
}

.image-list {
  margin: 20px auto 0;
  width: 520px;
  display: flex;
  justify-content: space-between;
}

.image-list__item {
  display: inline-block;
  height: 100px;
}

.image-list__item:hover {
  cursor: pointer;
}

.image-list__item.is-active {
  outline: 5px solid dodgerblue;
}

.image-list__item img {
  max-width: 100%;
  height: 100%;
}

.images-container {
  position: relative;
}

.prev-list, .next-list {
  position: absolute;
  top: calc(50% - 10px);
  height: 20px;
  width: 20px;
  background: transparent;
  transition: 0.3s;
}

.prev-list:hover, .next-list:hover {
  transform: scale(1.1);
}

.prev-list svg, .next-list svg {
  height: 100%;
  width: 100%;
}

.prev-list {
  left: 0;
}

.next-list {
  right: 0;
}
