@import "lib/pure-css-loader/css-loader";

body {
	position: relative; }

.overlay {
	display: none;
	height: 100vh;
	width: 100vw;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.7); }

.preview {
	height: 90vh;
	width: fit-content;
	margin: 5vh auto;
	padding: 10px;
	background-color: #FFFFFF;
	position: relative;
	img {
		height: 100%;
		max-width: 100%; } }

.btn-next, .btn-prev {
	position: absolute;
	display: block;
	top: calc(50% - 20px);
	height: 40px;
	width: 40px;
	background: transparent;
	color: white;
	transition: 0.3s;
	&:hover {
		transform: scale(1.1); }
	svg {
		width: 100%;
		height: 100%;
		fill: #fff; } }

.btn-prev {
	left: -50px; }

.btn-next {
	right: -50px; }

.container {
	width: 600px;
	height: 800px;
	margin: 0 auto;
	padding: 10px;
	overflow: hidden; }

.big-image {
	width: 580px;
	height: 600px;
	margin: 0 auto;
	&__img {
		width: 100%;
		&:hover {
			cursor: pointer; } } }

.image-list {
	margin: 20px auto 0;
	width: 520px;
	display: flex;
	justify-content: space-between;
	&__item {
		display: inline-block;
		height: 100px;
		&:hover {
			cursor: pointer; }
		&.is-active {
			outline: 5px solid dodgerblue; }
		img {
			max-width: 100%;
			height: 100%; } } }

.images-container {
	position: relative; }

.prev-list, .next-list {
	position: absolute;
	top: calc(50% - 10px);
	height: 20px;
	width: 20px;
	background: transparent;
	transition: 0.3s;
	&:hover {
		transform: scale(1.1); }
	svg {
		height: 100%;
		width: 100%; } }

.prev-list {
	left: 0; }

.next-list {
	right: 0; }
